
import { defineComponent, ref } from 'vue'
import { QrcodeStream, QrcodeDropZone, QrcodeCapture } from 'qrcode-reader-vue3'
import { QuickFetch } from '@/components/quick'

export default defineComponent({
    setup() {
        const qf = new QuickFetch();
        const location: any = ref(11);
        const status: any = ref(0);
        const name: any = ref("");
        function onInit (promise: any) {
            promise
            .catch(console.error);
        }
        function onDecode (result: any) {
            qf.Dataset().Add('content',result).AddObj('location',location);
            qf.Url('scan').Post(qf.GetDset()).then((res: any)=>{
                status.value = 0;
                if(res.message=='done'){
                    name.value = res.data.name;
                    codeDisplay(res.data);
                }else if(res.message=='invalid_code'){
                    name.value = '非法的QR Code';
                }
            });
        }
        function paintOutline (detectedCodes: any, ctx: any) {
            for(const detectedCode of detectedCodes){
                const [firstPoint, ...otherPoints] = detectedCode.cornerPoints
                ctx.strokeStyle = "rgba(14,165,233,0.8)";
                ctx.lineWidth = 10;
                ctx.beginPath();
                ctx.moveTo(firstPoint.x, firstPoint.y);
                for(const { x, y } of otherPoints){
                    ctx.lineTo(x, y);
                }
                ctx.lineTo(firstPoint.x, firstPoint.y);
                ctx.closePath();
                ctx.stroke();
            }
        }

        const accept_status = ['v2o', 'v3o', 'pcr', 'rat'];
        const description: any = ref('');
        const signal: any = ref(0);
        const status_list: any = {
            "0000": "帳號尚未通過認證，請靜候審核",
            "xxxx": "身份不合，請洽體育組",
            "xxxo": "健康狀態審核中",
            "v1x": "已接種 1 劑新冠疫苗 (未滿14天)",
            "v1o": "已接種 1 劑新冠疫苗",
            "v2x": "已接種 2 劑新冠疫苗 (未滿14天)",
            "v2o": "已接種 2 劑新冠疫苗",
            "v2i": "已接種 2 劑新冠疫苗 (超過12週)",
            "v3o": "已接種 3 劑新冠疫苗",
            "pcr": "PCR陰性證明",
            "rat": "快篩陰性證明",
            "px1": "無效的PCR陰性證明",
            "px2": "非期限內的PCR陰性證明",
            "rx1": "無效的快篩陰性證明",
            "rx2": "非期限內的快篩陰性證明",
            "x": "尚未填寫健康聲明書",
            "o": "已填寫健康申明書",
        };

        function codeDisplay(input: any){
            if(input.status=='0000' || input.status=='xxxx' || input.status=='xxxo'){
                description.value = input.status;
                signal.value = 0;
            }else{
                signal.value = 0;
                if(input.status.substr(3,1)=='x'){
                    description.value = input.status.substr(3,1);
                    signal.value = 0;
                }else{
                    description.value = input.status.substr(0,3);
                    signal.value = 1;
                    if(accept_status.indexOf(input.status.substr(0,3))>-1){
                        signal.value = 2;
                    }
                }
            }
        }
        return {
            status,
            location,
            onInit,
            onDecode,
            paintOutline,
            status_list,
            description,
            signal,
            name,
        }
    },
    components: {
        QrcodeStream,
        //QrcodeDropZone,
        //QrcodeCapture
    },
})
